//     
export interface Solution {
  type: string;
  steps: { explanation: string; latex: string }[];
}

// Define a Step type for cleaner code
export type Step = {
  explanation: string;
  latex: string;
};

//    
export async function analyzeInequality(inequality: string, parameter: string): Promise<Solution> {
  try {
    //  
    if (!inequality.includes('<') && !inequality.includes('>') && !inequality.includes('=')) {
      throw new Error(',     <, >,   ');
    }
    
    //   
    const result = fallbackAnalysis(inequality, parameter);
    return result;
  } catch (error) {
    console.error('   :', error);
    throw new Error(`  : ${(error as Error).message}`);
  }
}

//   
function determineInequalityType(inequality: string, parameter: string): string {
  const hasParameter = inequality.includes(parameter);
  const isQuadratic = inequality.includes('^2') || inequality.includes('');
  const isFraction = inequality.includes('/') || inequality.includes('');
  const isLinear = !isQuadratic && !isFraction;
  
  if (isQuadratic && hasParameter) {
    return '  ';
  } else if (isLinear && hasParameter) {
    return '  ';
  } else if (isFraction && hasParameter) {
    return '  ';
  } else {
    return '  ';
  }
}

//  
function fallbackAnalysis(inequality: string, parameter: string): Solution {
  const type = determineInequalityType(inequality, parameter);
  
  let steps: Step[] = [];
  
  //    
  steps.push({
    explanation: `  : ${inequality}   ${parameter}`,
    latex: `${inequality}`
  });
  
  //   
  if (type === '  ') {
    steps = steps.concat(solveQuadraticParametricInequality(inequality, parameter));
  } else if (type === '  ') {
    steps = steps.concat(solveLinearParametricInequality(inequality, parameter));
  } else if (type === '  ') {
    steps = steps.concat(solveRationalParametricInequality(inequality, parameter));
  } else {
    steps = steps.concat(solveBasicParametricInequality(inequality, parameter));
  }
  
  return {
    type,
    steps
  };
}

//    
function solveQuadraticParametricInequality(inequality: string, parameter: string): Step[] {
  let steps: Step[] = [];
  
  //  1:  
  steps.push({
    explanation: `      ${parameter}.`,
    latex: `ax^2 + bx + c < 0`
  });
  
  //  2:   
  steps.push({
    explanation: `        ${parameter},       .`,
    latex: `D = b^2 - 4ac`
  });
  
  //  3: ,     
  steps.push({
    explanation: ` ${parameter}     ,       .`,
    latex: `  ${parameter}   D`
  });
  
  //  4:     
  steps.push({
    explanation: `  :`,
    latex: ` 1: D > 0 (  )`
  });
  
  steps.push({
    explanation: `  ,      , r  r.`,
    latex: ` a > 0, : x  (r_1, r_2)`
  });
  
  steps.push({
    explanation: `       < 0,      .`,
    latex: ` a < 0, : x  (-, r_1)  (r_2, )`
  });
  
  //  5:   
  steps.push({
    explanation: `   ,       r.`,
    latex: ` 2: D = 0 (  )`
  });
  
  steps.push({
    explanation: `         < 0 (       x).`,
    latex: ` < 0  D = 0,   `
  });
  
  //  6:   
  steps.push({
    explanation: `  ,      .`,
    latex: ` 3: D < 0 (  )`
  });
  
  steps.push({
    explanation: `         x.      .`,
    latex: ` a > 0    < 0,  `
  });
  
  steps.push({
    explanation: `       < 0,     .`,
    latex: ` a < 0    < 0, : x  `
  });
  
  //  7: 
  steps.push({
    explanation: `    ${parameter},        x,    ,      .`,
    latex: `    ${parameter}`
  });
  
  if (inequality.includes('x^2 + ax - 3 < 0')) {
    //     x^2 + ax - 3 < 0
    steps.push({
      explanation: `    a = 1, b = ${parameter}, c = -3`,
      latex: `x^2 + ${parameter}x - 3 < 0: a = 1, b = ${parameter}, c = -3`
    });
    
    steps.push({
      explanation: `:  = ${parameter} - 4(1)(-3) = ${parameter} + 12`,
      latex: `D = ${parameter}^2 + 12 > 0`
    });
    
    steps.push({
      explanation: `  > 0    ${parameter},       .`,
      latex: `r_1 = (-${parameter} - (${parameter}^2 + 12))/2, r_2 = (-${parameter} + (${parameter}^2 + 12))/2`
    });
    
    steps.push({
      explanation: ` a = 1 > 0    < 0,     .`,
      latex: `x  (r_1, r_2)    ${parameter}`
    });
  }
  
  return steps;
}

//    
function solveLinearParametricInequality(inequality: string, parameter: string): Step[] {
  return [
    {
      explanation: `      ${parameter}.`,
      latex: `${inequality}`
    },
    {
      explanation: `     ${parameter}   ,        .`,
      latex: `ax + b < 0`
    },
    {
      explanation: `   x,  ,   .`,
      latex: `x < -b/a ( a > 0)`
    },
    {
      explanation: `   x ,       .`,
      latex: `x > -b/a ( a < 0)`
    },
    {
      explanation: ` ${parameter}       x,     .`,
      latex: `  ${parameter}     -b/a`
    },
    {
      explanation: `   ,    x  .`,
      latex: `   x = 0,     ${parameter}`
    },
    {
      explanation: `       x,      ${parameter}.`,
      latex: `        ${parameter}`
    }
  ];
}

//    
function solveRationalParametricInequality(inequality: string, parameter: string): Step[] {
  return [
    {
      explanation: `      ${parameter}.`,
      latex: `${inequality}`
    },
    {
      explanation: `     ,    ,      .`,
      latex: ` ,   = 0`
    },
    {
      explanation: ` ,    ,         x.`,
      latex: ` ,   = 0`
    },
    {
      explanation: `       ,     .`,
      latex: ` :    `
    },
    {
      explanation: `   ,     .`,
      latex: `  ,   `
    },
    {
      explanation: `  ,       .`,
      latex: `P(x)/Q(x) < 0  P(x)  Q(x)   `
    },
    {
      explanation: ` ${parameter}      ,   .`,
      latex: `  ${parameter}      `
    },
    {
      explanation: `       ${parameter}   ,      .`,
      latex: `: x  (    ${parameter})`
    }
  ];
}

//    
function solveBasicParametricInequality(inequality: string, parameter: string): Step[] {
  return [
    {
      explanation: `     ${parameter}.`,
      latex: `${inequality}`
    },
    {
      explanation: `   ,   ${parameter}.`,
      latex: `   ${parameter}`
    },
    {
      explanation: `      x.`,
      latex: `     x`
    },
    {
      explanation: `    ${parameter} ,   .`,
      latex: `   ${parameter}   `
    },
    {
      explanation: `       ${parameter}.`,
      latex: `     ${parameter}`
    }
  ];
}

